      <h1 class="text-center section-header">Company Profile</h1>
      <div class="section-header-underline"></div>
      <div class="section-header-padding-bottom"></div>
      <div class="company-profile-container">
        <div class="row section-content-container">
          <div class="col-md-4 col-sm-4 col-xs-12" v-for="user in users.slice(indexStart, indexEnd)" v-bind:key="user._id">
            <div class="card text-center" v-b-modal="`modal-${user._id}`">
              <img v-bind:src="require(`../assets/images/${user.photo_name}`)" class="card-img-top" />
              <div class="card-body">
                <h5 class="card-title">{{user.first_name}} {{user.last_name}}</h5>
                <p class="container-fluid card-text">{{user.title}}</p>
              </div>
            </div>
            <b-modal v-bind:id="`modal-${user._id}`">
              <div slot="modal-title">
                {{user.first_name}} {{user.last_name}}
                <p>{{user.title}}</p>
              </div>
              <div>
                <p>{{user.bio}}</p>
                <p v-if="user.quote != ''">"{{user.quote}}"</p>
                <div v-for="bulletPoint in user.bullet_points" v-bind:key="bulletPoint._id">
                  <ul v-if="user.bullet_points.length >= 1 && user.bullet_points[0] != ''">
                    <li>{{bulletPoint}}</li>
                  </ul>
                </div>
              </div>
              <div slot="modal-footer" class="w-100 modal-close-btn">
                <b-button
                  size="sm"
                  class="float-right main-background-color"
                  @click="show=false"
                >
                  Close
                </b-button>
              </div>
            </b-modal>
          </div>
          <a class="prev" @click="prevRow()">
            <font-awesome-icon :icon="{ prefix: 'fas', iconName: 'angle-left' }" />
          </a>
          <a class="next" @click="nextRow()">
            <font-awesome-icon :icon="{ prefix: 'fas', iconName: 'angle-right' }" />
          </a>
        </div>
      </div>
    </div>

    <!-- <form enctype="multipart/form-data">
