<script>
import UserService from '../services/UserService'

export default {
  name: 'CompanyProfile',
  data () {
    return {
      users: [],
      firstName: null,
      lastName: null,
      title: null,
      bio: null,
      quote: null,
      bulletPoints: null,
      photo: null,
      errors: '',
      addUser: UserService.addUser,
      slide: 0,
      sliding: null,
      currentRow: 1,
      indexStart: 0,
      indexEnd: 3,
      isLastRow: false,
      numRows: 0,
      additionalRow: 0,
      finalIndexRemainder: 0,
      rowRemainder: false
    }
  },
  async created () {
    try {
      this.users = await UserService.getUsers();
      this.calculateRows(this.users);
    } catch (err) {
      this.error = err.message;
    }
  },
  methods: {
    calculateRows(userArray) {
      if(userArray.length % 3 === 0) {
        this.numRows = userArray.length / 3;
        this.rowRemainder = false;
      } else {
        this.numRows = Math.trunc(userArray.length /3) + 1;
        this.finalIndexRemainder = userArray.length % 3;
        this.rowRemainder = true;
      }
    },
    nextRow() {
      this.currentRow += 1;
      this.additionalRow += 1;

      if (this.currentRow < this.numRows) {
        this.indexStart = this.additionalRow * 3;
        this.indexEnd = (this.additionalRow * 3) + 3;  
      }

      if (this.currentRow === this.numRows) {
        this.isLastRow = true;
        this.indexStart = this.additionalRow * 3;
        if (this.rowRemainder) {
          this.indexEnd = (this.additionalRow * 3) + this.finalIndexRemainder;
        } else {
          this.indexEnd = (this.additionalRow * 3) + 3;  
        }
      }

      if (this.currentRow > this.numRows) {
        this.isLastRow = false;
        this.indexStart = 0;
        this.indexEnd = 3;
        this.currentRow = 1;
        this.additionalRow = 0;
      }
    },
    prevRow() {
      if (this.currentRow === 1) {
        this.currentRow = this.numRows;
        this.additionalRow = this.numRows - 1;
      } else {
        this.currentRow -= 1;
        this.additionalRow -= 1;
      }

      if (this.currentRow < this.numRows) {
        this.indexStart = this.additionalRow * 3;
        this.indexEnd = (this.additionalRow * 3) + 3;  
      }

      if (this.currentRow === this.numRows) {
        this.isLastRow = true;
        this.indexStart = this.additionalRow * 3;
        if (this.rowRemainder) {
          this.indexEnd = (this.additionalRow * 3) + this.finalIndexRemainder;
        } else {
          this.indexEnd = (this.additionalRow * 3) + 3;  
        }
      }

      if (this.currentRow > this.numRows) {
        this.isLastRow = false;
        this.indexStart = 0;
        this.indexEnd = 3;
        this.currentRow = 1;
        this.additionalRow = 0;
      }
    },
    // processPhoto(event) {
    //   this.photo = event.target.files[0];
    //   const files = event.target.files
    //   if (!files) {
    //     return
    //   }
    //   let filename = files[0].name
    //   if (filename.lastIndexOf('.') <= 0) {
